/** Media Queries **/
.elementos-caja-superior {
  display: flex;
  flex-flow: row;
  justify-content: center;
  align-items: center;
  padding: 1rem;
}

.elementos-caja-inferior {
  font-weight: bolder;
  color: black;
  font-size: 2.5rem;
  margin-top: 0;
}
.elementos-caja-inferior h4 {
  margin: 0;
}

.container-reportes {
  margin-bottom: 2rem;
}

.encabezado-reportes {
  display: none;
}

.fondo-7dias {
  background-color: #0fb7ef;
}

.fondo-14dias {
  background-color: aliceblue;
}

.fondo-30dias {
  background-color: #0fb7ef;
}

@media (min-width: 1080px) {
  .div-reportes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
    margin-top: 2rem;
    text-align: center;
  }
  .div-reportes .caja-riego {
    background-color: #ec9b22;
    border-radius: 12px;
  }
  .div-reportes .caja-humedadSuelo {
    background-color: #03c74e;
    border-radius: 12px;
  }
  .div-reportes .caja-temperatura {
    background-color: #23b8fa;
    border-radius: 12px;
  }
  .div-reportes .caja-humedadRelativa {
    background-color: #0fb7ef;
    border-radius: 12px;
  }
  .div-reportes .icon {
    width: 150px;
    height: auto;
  }
  .div-reportes p {
    font-size: 2rem;
    font-weight: bold;
  }
  .encabezado-reportes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
    margin-top: 2rem;
    text-align: center;
  }
  .fondo-7dias {
    background-color: aliceblue;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
    margin-top: 2rem;
    text-align: center;
  }
  .fondo-14dias {
    background-color: aliceblue;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
    margin-top: 2rem;
    text-align: center;
  }
  .fondo-30dias {
    background-color: aliceblue;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
    margin-top: 2rem;
    text-align: center;
  }
}